export default {
  name: "AirdropAddressCard",
  props: {
    showAirdropAddress: {
      type: Boolean,
      default: false
    },
    airdropList: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      MintNum: 0,
      visables: this.showAirdropAddress,
    }
  },
  watch: {
    showAirdropAddress(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeAirdropAddressCard", false);
    }
  }
}

